import React,{useEffect,useState,useContext} from "react"
import Helmet from "../commponents/Helmet/Helmet";
import RightBar from "../commponents/Right/RightBar";
import { uidContext } from "../commponents/AppContext"
import FollowHandle from "../commponents/profile/FollowHandle";
import Posts from "../commponents/Posts/Posts";
import {isEmpty} from "../commponents/Utils"
import { Avatar } from "@mui/material";
import { GetAllposts } from '../redux/slices/Allposts';
import { getusersAction } from '../redux/slices/Getusers';
import {Link} from "react-router-dom";
import {SERVER_URL} from "../Domain"
import { getOrientation } from 'get-orientation/browser'
import CropEasy from "../commponents/crop/CropEasy"
import axios from "axios"
import { useDispatch } from "react-redux";
import {getRotatedImage} from "../commponents/crop/utils/cropImage"
import {useSelector} from "react-redux"
import Card from "../commponents/Posts/Card"
const ProfileOther = ({socket})=>{
        const user = useSelector((state) => state.userInfo)
        const usersData = useSelector((state) => state.getusers)
        const Allpost = useSelector((state) => state.Allpost)
        const [hisFollowers ,setHisFollowers] =useState()
        const [hisLikes ,setHisLikes] =useState()
        const [hisPosts ,sethisPosts] =useState()
        const [editprofile,seteditprofile] = useState(false)
        const [userinfo,setUser]=useState()
        const [photoURL,setPhotoURL]=useState(user?.picture)
        const [openCrop,setOpenCrop]=useState(false)
        const [suivante,setsuivante] = useState(false)
        const dispatch = useDispatch()
        const uid = useContext(uidContext)
        const ORIENTATION_TO_ANGLE = {
        '3': 180,
        '6': 90,
        '8': -90,
        }
          function readFile(file) {
          return new Promise((resolve) => {
          const reader = new FileReader()
          reader.addEventListener('load', () => resolve(reader.result), false)
          reader.readAsDataURL(file)
          })
          }
      useEffect(()=>{
              if(hisFollowers?.length > 0){
              const carousel = document.querySelector(".carousel");
              const arrowsBtns = document.querySelectorAll(".himfollowers i");
              arrowsBtns.forEach((btn) => {
              btn.addEventListener("click", (e) => {
              console.log(e.target.id);
              if (e.target.id === "left") {
              carousel.scrollLeft -= 10;
              } else {
              carousel.scrollLeft += 10;
              }
              });
              });
              let isDragging = false,
              startX,
              startScrollLeft;
              const dragstart = (e) => {
              isDragging = true;
              carousel.classList.add("dragging");
              startX = e.pageX;
              startScrollLeft = carousel.scrollLeft;
              };
              const dragstop = () => {

              isDragging = false;
              carousel.classList.remove("dragging");
              };
              const dragging = (e) => {
              if (!isDragging) return;
              carousel.scrollLeft = startScrollLeft - (e.pageX - startX);
              };
              carousel.addEventListener("mousedown", dragstart);
              carousel.addEventListener("mousemove", dragging);
              document.addEventListener("mouseup", dragstop);
              }
  },[hisFollowers?.length])

  //fetch all userdata from data base
  useEffect(()=>{
    if(uid){
    const fetchUserData = async () => {
      if (uid !== "no token") {
        await axios({
          method: "get",
          url: `${SERVER_URL}/api/user/${uid}`,
          withCredentials: true,
        })
          .then(({data}) => {
           setUser(data)
          })
          .catch(() => console.log("no data found"))
      }
    }
    fetchUserData()
    }
    //fetch all data's user
        const fetchUserAllData = async () => {
      if (uid !== "no token") {
        await axios({
          method: "get",
          url: `${SERVER_URL}/api/user`,
          withCredentials: true,
        })
          .then((res) => {
            dispatch(getusersAction.GetuAllsers(res.data))
          })
          .catch(() => dispatch(getusersAction.GetuAllsers({ message: "no user exist now" })))
      }
    }
    fetchUserAllData()
    //fetch all post data
        const fetchUserAllDataPost = async () => {
      if (uid !== "no token") {
        await axios({
          method: "get",
          url: `${SERVER_URL}/api/post`,
          withCredentials: true,
        })
          .then(({data}) => {
            dispatch(GetAllposts.GetAll(data))
            console.log("fetch again from profile outher ")
          })
          .catch(() => dispatch(GetAllposts.GetAll({ message: "no post exist now" })))
      }
    }
    fetchUserAllDataPost() 
  },[uid])
         useEffect(()=>{
          if((uid !== "no token" || Allpost) && !isEmpty(usersData) ){
            
            var myFollowers = usersData?.filter((user)=> user.following.includes(uid))
            console.log(myFollowers)
            setHisFollowers(myFollowers)
            var myPosts  = Allpost.filter((post)=>post.posterId === uid)
            sethisPosts(myPosts)
            console.log(myPosts)
          
          var count = 0;
          for (let index = 0; index < hisPosts?.length; index++) {
          count = count + hisPosts[index].likers.length
           }
           setHisLikes(count)
        }
       },[uid,Allpost])
          const handlePicture = async(e)=>{
            const file = e.target.files[0];
          if(file){
            let imageDataUrl = await readFile(file)
                try {
                // apply rotation if needed
                const orientation = await getOrientation(file)
                const rotation = ORIENTATION_TO_ANGLE[orientation]
                if (rotation) {
                imageDataUrl = await getRotatedImage(imageDataUrl, rotation)
                }
                } catch (e) {
                console.log('failed to detect the orientation')
                }
            setOpenCrop(true)
            setPhotoURL(imageDataUrl)
          }
        }
   return(
    <Helmet title={user?.pseudo}>
    <div className="container">
      <div className="wrapper">
        <div style={{display:"flex",flexDirection:"column",gap:"1rem"}}>
        <div className="profile">
          <div className="image">
            <img src={userinfo?.picture} alt={userinfo && userinfo?._id}/>
          </div>
          <div className="wrapper-2">
          <div style={
          {
          width:"9rem",
          height:"9rem",
          overflow:"hidden",
          borderRadius:"50%",
          position:"relative",
          top:"-70px",
          left:"20px",
          border:"6px solid var(--color-white)"

          }
        }>
           <Avatar 
           style={{
            display:"absolute",
            top:"0",
            left:"0",
            width:"100%",
            height:"100%"
           }}
           src={userinfo?.picture} alt={userinfo?.pseudo}/>
         </div> 
         <div className="infos">
          <h4>{userinfo?.pseudo}</h4>
          <span>{"tudiant(e)  univeriste polytechinique de gitega(UPG)"}</span>
          <small>kinyinya,ruyigi,burundi</small>
          <div className="followFollowing">
          <span>{userinfo?.followers.length} followers</span> 
          <span>{userinfo?.following.length} following</span> 
          <span>{hisPosts?.length} Posts</span> 
          <span>{hisLikes} Likes</span> 
          </div>
          <span>la vie est plus bon que tu le pense mais aussi est plud dur que bon buy jeneiro hurley</span>
            <div className="action">
            <FollowHandle
               idTofollow={uid}
               type={"suggestion"}
                                />
                                {" "}
           <button className=" btn-new" onClick={()=>seteditprofile(true)}>Edit Profile</button>
        </div>
         </div>
          </div>
        </div>
         {hisFollowers?.length > 0 && <div className="himfollowers">
         {hisFollowers?.length > 3 && <i className="ri-arrow-drop-left-line" id="left"></i>}
         <div className="carousel">
         {
            hisFollowers?.map((user,index)=>(
         <div className="request" key={index}>
            <div className="info">
               <div className="profile-picture">
               <Avatar src={user?.picture} alt="aa" />
               </div>
               <div>
              <Link className="linkProfile" to={`/profile/${user?._id}`}><h5 className="noum">{user?.pseudo}</h5></Link>
            <p className="text-muted">
               {user?.following.length} following
            </p>
            </div>
            </div>
            <div className="action">
            <FollowHandle
            idTofollow={user._id}
            type={"suggestion"}
                           />
            <button className=" btn-new">gretting</button>
           </div>
            </div>
         ))}
         </div>
         {hisFollowers?.length > 3 && <i className="ri-arrow-drop-right-line" id="right"></i>}
         </div>}
        <div>
        <Posts socket={socket} type={"hisprofile"} paramsId={uid}/>
        </div>
        </div>
        <div className="leftProfile">{user && <RightBar user={user}/>}</div>
      </div>
    </div>
    {
     openCrop &&
     <div className="popupUpdateProfileCrop">
      <CropEasy {...{photoURL,setOpenCrop}}/>
    </div>
      
    }

    {
      (editprofile) &&
          <div className="popupUpdateProfile">
      <div className="un">
        <div className="un-un"><h3>User infos</h3></div>
        <div className="un-deux">
          <input type="text" placeholder="full name" />
          <button className="btnprofile">update</button>
        </div>
        <div className="un-trois">
          <input type="text" placeholder="number phone ie: +257 11449900" />
          <button className="btnprofile">update</button>
        </div>
        <div className="un-quatre">
          <input type="text" placeholder="biographique"  />
          <button className="btnprofile">update</button>
        </div>
        <div className="un-cinq">
          <input type="text" placeholder="Your proffessional" />
          <button className="btnprofile">update</button>
        </div>
        <div className="un-six">
          <input type="text" placeholder="Your country" />
          <button className="btnprofile">update</button>
        </div>
        <div className="un-six">
          <input type="text" placeholder="where didu study???" />
          <button className="btnprofile">update</button>
        </div>
        <div className="un-six">
          <select>
            <option>Masculin</option>
            <option>feminin</option>
          </select>
          <button className="btnprofile">update</button>
        </div>
        <div className="un-un"><h3>Your social media</h3></div>
          <div className="un-six">
          <input type="text" placeholder="Add link of your social media like ie: https://www.chatburundi.com/janeirohurley" />
          <button className="btnprofile">Add</button>
          <div className="social-media">
            <div className="media-1"><i className="ri-facebook-circle-fill"></i><Link to="profile">facebook</Link><span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-twitter-fill"></i>twitter <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-instagram-fill"></i>instagram <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-youtube-fill"></i>youtube<span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-whatsapp-fill"></i>whatsapp <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-whatsapp-fill"></i>whatsapp <span><i className="ri-close-circle-fill"></i></span></div> 
          </div>
        </div>
        {
          suivante &&
        <div className="suivante">
        <button className="btnprofile precedente" onClick={()=>setsuivante(false)}><i className="ri-arrow-left-line"></i> precedente</button>
        </div>
        }

        <button className="btnprofile btnsuivante" onClick={()=>setsuivante(true)}> suivante <i className="ri-arrow-right-line"></i></button>
      </div>
      <div className="deux">
        <div className="deux-un"><h3>Current profile</h3></div>
            <div className="profile">
            <div className="image">
            <div className="popounderphoto">
              <div className="icon-input">
                <input type="file" title="update picture" onChange={(e)=>handlePicture(e)} />
                <i className="ri-pencil-line"></i>
              </div>
            </div>
            <img src={userinfo?.picture} alt={userinfo && userinfo?._id}/>
            </div>
            <div className="wrapper-2">
            <div style={
            {
            width:"9rem",
            height:"9rem",
            overflow:"hidden",
            borderRadius:"50%",
            position:"relative",
            top:"-70px",
            left:"20px",
            border:"6px solid var(--color-white)"

            }
            }>
            <div className="popounderphoto roundprofile">
            <div className="icon-input2">
            <input type="file" title="update picture" onChange={(e)=>handlePicture(e)} />
            <i className="ri-pencil-line"></i>
            </div>
            </div>
            <Avatar 
            style={{
            display:"absolute",
            top:"0",
            left:"0",
            width:"100%",
            height:"100%"
            }}
            src={userinfo?.picture} alt={userinfo?.pseudo}/>
            </div> 
            <div className="infos">
            <h4>{userinfo?.pseudo}</h4>
            <span><i className="ri-building-4-line"></i>{" tudiant(e)  univeriste polytechinique de gitega(UPG)"}</span>
            <small><i className="ri-map-pin-2-line"></i> kinyinya,ruyigi,burundi</small>
            <span> bio : la vie est plus bon que tu le pense mais aussi est plud dur que bon buy jeneiro hurley</span>
            <div className="followFollowing">
            <span>{userinfo?.followers.length} followers</span> 
            <span>{userinfo?.following.length} following</span> 
            <span>{hisPosts?.length} Posts</span> 
            <span>{hisLikes} Likes</span> 
            </div>
                      <div className="social-media">
            <div className="media-1"><i className="ri-facebook-circle-fill"></i><Link to="profile">facebook</Link><span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-twitter-fill"></i>twitter <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-instagram-fill"></i>instagram <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-youtube-fill"></i>youtube<span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-whatsapp-fill"></i>whatsapp <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-whatsapp-fill"></i>whatsapp <span><i className="ri-close-circle-fill"></i></span></div> 
          </div>
            </div>
            </div>
            </div>

      </div>
    </div>
    }
     </Helmet>
    )
}

export default ProfileOther;